@charset "UTF-8";
/*добавляем переменные и миксины*/
/*базовые стили*/
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Mulish:wght@700&display=swap");
html,
body {
  box-sizing: border-box;
  height: 100%;
  padding: 0;
  margin: 0;
}

body {
  color: rgb(23, 23, 23);
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  background-color: rgb(19, 19, 19);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  box-sizing: border-box;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.main-header,
.main-footer {
  flex-grow: 0;
  flex-shrink: 0;
}

.main-container {
  flex-grow: 1;
}

/*стили блоков*/
.button {
  background-color: rgb(194, 171, 129);
  color: rgb(255, 255, 255);
  font-weight: 700;
  font-size: 12px;
  line-height: 18px;
  padding: 15px 40px 14px;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  font-family: "Montserrat", "Arial", sans-serif;
  border: 0;
  transform: skew(-17deg);
  position: relative;
  left: 7px;
}
.button::after {
  content: "";
  width: 20px;
  height: 47px;
  background: url("../img/stack.svg#line-icon");
  display: inline-block;
  position: absolute;
  top: 0;
  right: -16px;
  transform: skew(17deg);
}
.button:hover {
  background-color: rgba(194, 171, 129, 0.8);
}
.button:active, .button:focus {
  background-color: rgba(194, 171, 129, 0.3);
}
.button:disabled {
  background-color: rgba(194, 171, 129, 0.1);
}
.button--big {
  padding: 22px 78px 20px;
  left: -7px;
}
@media (min-width: 1200px) {
  .button--big {
    padding: 15px 60px 14px;
  }
  .button--big::after {
    width: 20px;
    height: 47px;
    right: -16px;
  }
}
.button--big::after {
  width: 26px;
  height: 60px;
  right: -23px;
}
@media (min-width: 768px) {
  .button {
    padding: 21px 73px 20px;
  }
  .button::after {
    width: 28px;
    height: 59px;
    right: -22px;
  }
}
@media (min-width: 1200px) {
  .button {
    padding: 21px 70px 21px;
  }
  .button::after {
    width: 26px;
    height: 60px;
    right: -22px;
  }
}

.button__text {
  transform: skew(17deg);
  position: relative;
  display: inline-block;
}

.main-header {
  background-color: rgb(19, 19, 19);
  padding: 13px 16px 35px;
  min-height: 96px;
}
.main-header--nojs {
  height: auto;
}
@media (min-width: 768px) {
  .main-header {
    min-height: 141px;
    height: auto;
    padding: 30px 16px 70px;
  }
}
@media (min-width: 1200px) {
  .main-header {
    display: flex;
    padding: 28px 101px 35px;
  }
}

.main-header--nojs .main-nav__toggle {
  display: none;
}

.main-header--nojs .main-nav {
  position: static;
}

.main-header--nojs .main-nav__wrapper, .main-header--nojs .main-nav--closed .main-nav__wrapper {
  position: static;
  display: block;
  width: auto;
  height: auto;
}

@media (max-width: 767px) {
  .main-header--nojs .main-nav--closed .main-nav__list {
    display: block;
  }
}
.main-header__logo {
  position: absolute;
  top: 13px;
  right: 16px;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-header__logo {
    position: absolute;
    top: 30px;
    left: 23%;
    width: 156px;
    height: 60px;
    z-index: 1;
  }
  .main-header__logo img {
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .main-header__logo {
    order: -1;
    position: static;
    margin-right: 75px;
  }
}

.main-nav {
  background-color: rgb(19, 19, 19);
}
@media (max-width: 1199px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--closed .main-nav__toggle {
    background: url("../img/stack.svg#menu-icon");
  }
  .main-nav--opened .main-nav__list {
    display: block;
    position: absolute;
    top: 116px;
    left: 0;
    width: 100vw;
    height: calc(100vh - 116px);
    z-index: 2;
    padding: 45px 50px 10px;
    background-color: rgb(19, 19, 19);
  }
  .main-nav--opened .main-nav__toggle {
    background: url("../img/stack.svg#close-icon");
    width: 24px;
    height: 20px;
    top: 50px;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
  .main-nav--opened .main-nav__wrapper {
    display: block;
  }
  .main-nav--opened .main-nav__list {
    display: block;
    position: static;
    top: 116px;
    left: 0;
    width: auto;
    height: auto;
    z-index: 2;
    padding: 0;
  }
  .main-nav--opened .main-nav__toggle {
    top: 26px;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    width: calc(100% - 231px);
  }
}

.main-nav__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100vw;
  height: 100vh;
  padding: 62px 18px 10px;
  background-color: rgb(19, 19, 19);
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    position: initial;
    width: 100%;
    height: auto;
    padding: 0;
    display: flex;
    justify-content: end;
  }
}
@media (min-width: 1200px) {
  .main-nav__wrapper {
    justify-content: space-between;
    align-items: center;
  }
}

.main-nav__toggle {
  background: url("../img/stack.svg#menu-icon");
  width: 37px;
  height: 31px;
  border: 0;
  position: absolute;
  top: 20px;
  left: 15px;
  cursor: pointer;
  z-index: 3;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:focus {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    top: 44px;
    left: 49px;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  margin-bottom: 45px;
}
@media (min-width: 1200px) {
  .main-nav__list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 0;
  }
  .main-nav__list::after {
    display: none;
  }
}

@media (min-width: 1200px) {
  .main-nav__item {
    margin-right: 43px;
  }
}

.main-nav__item a {
  color: rgb(19, 19, 19);
  text-transform: uppercase;
  font-size: 20px;
  line-height: 30px;
  font-weight: 700;
  padding: 8px 0 7px;
  display: block;
  text-shadow: -1px -1px 0 rgb(255, 255, 255), 1px -1px 0 rgb(255, 255, 255), -1px 1px 0 rgb(255, 255, 255), 1px 1px 0 rgb(255, 255, 255);
}
.main-nav__item a:hover {
  opacity: 0.5;
}
.main-nav__item a:focus, .main-nav__item a:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__item a {
    font-size: 36px;
    line-height: 54px;
  }
}
@media (min-width: 1200px) {
  .main-nav__item a {
    text-shadow: none;
    color: rgb(255, 255, 255);
    font-size: 14px;
    line-height: 21px;
    transition: 0.5s;
  }
  .main-nav__item a:hover {
    opacity: 1;
    color: rgb(194, 171, 129);
  }
}

.main-nav__item--current a {
  color: #000000;
}

@media (min-width: 768px) {
