.modal {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 320px;
  transform: translate(-50%, -50%);
  background-color: rgb(23, 23, 23);
  text-align: center;
  padding: 20px 20px 30px;
  z-index: 6;
  color: rgb(255, 255, 255);
  box-shadow: 0px 0px 10px rgb(23, 23, 23);
}
@media (min-width: 768px) {
  .modal {
    width: 520px;
  }
}

.modal__title {
  margin: 0;
  padding: 0;
  font-size: 20px;
  line-height: 30px;
  text-transform: uppercase;
  margin-bottom: 30px;
  text-align: left;
}
@media (min-width: 768px) {
  .modal__title {
    font-size: 30px;
    line-height: 40px;
  }
}

.modal__close {
  font-size: 0;
  position: absolute;
  top: 25px;
  right: 15px;
  background: url("../img/stack.svg#close-icon");
  width: 24px;
  height: 20px;
  border: 0;
  cursor: pointer;
}

.modal__success {
  display: none;
  text-align: left;
  color: rgb(194, 171, 129);
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 700;
}

.modal-fade {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  opacity: 0.9;
  display: none;
  z-index: 5;
}

