}

.form__button {
  margin-top: 15px;
}

.input {
  border: 1px solid rgb(255, 255, 255);
  padding: 10px 15px;
  appearance: none;
  -webkit-appearance: none;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
}
.input::placeholder {
  color: rgb(23, 23, 23);
  opacity: 0.3;
}
.input:active, .input:focus {
  box-shadow: 0 0 5px 4px inset rgb(23, 23, 23);
}
.input:hover {
  box-shadow: 0 0 5px 4px inset rgb(23, 23, 23);
}
.input--error {
  border-color: red;
  box-shadow: 0 0 5px 4px inset red;
}